import React, { Component } from 'react';

class Recruit extends Component {
	constructor(){
		super();
		this.state={
			nowJob : 0
		};
		this.getJob = this.getJob.bind(this);
		this.toggleJob = this.toggleJob.bind(this);
	}
	componentWillMount() {
		this.getJob();
	}
	getJob(){
		this.setState({
			jobList:[
			{name:'PHP',
			resp:'<p>1</p><p>2</p><p>3</p><p>4</p><p>5</p>',
			requirment:'<p>1ThinkPHP/PHP+MYSQL  WEB MVC<p><p>2JavaScriptAjaxCssHTMLJQueryJS<p><p>4PC<p><p>5LinuxshellWindowsServer<p><p>6<p>'
			},{
			name:'',
			resp:'<p>1</p><p>2</p><p>3</p><p>4</p><p>5</p>',
			requirment:'<p>1ThinkPHP/PHP+MYSQL  WEB MVC<p><p>2JavaScriptAjaxCssHTMLJQueryJS<p><p>4PC<p><p>5LinuxshellWindowsServer<p><p>6<p>'
			},{
			name:'',
			resp:'<p>1</p><p>2</p><p>3</p><p>4</p><p>5</p>',
			requirment:'<p>1ThinkPHP/PHP+MYSQL  WEB MVC<p><p>2JavaScriptAjaxCssHTMLJQueryJS<p><p>4PC<p><p>5LinuxshellWindowsServer<p><p>6<p>'
			}]
		});
	}
	toggleJob(e){
		console.log(e);
		const index =  e.currentTarget ? parseInt(e.currentTarget.getAttribute("index")) : -1;
		if(index != -1 && index != this.state.nowJob){
			this.setState({
				nowJob: index
			});
		}
	}
	render() {
		let job = new Array();
		job = this.state.jobList.map((item,index) => {
			return <li className={`job-item ${this.state.nowJob == index ? "current" : ""}`} onClick={this.toggleJob} index={index} key={index}>{item.name}</li>	
		});
		let requirment = {
			__html:this.state.jobList[this.state.nowJob].requirment,
		};
		let resp = {
			__html:this.state.jobList[this.state.nowJob].resp,
		};
		return (
				<div className="recruit-box">
					<div className="recruit-header inside-header">
						<span className="recruit-logo inside-logo iconfont icon-zhaopin"></span> <span className="inside-des">/</span>
					</div>
					<div className="job-list">
						<div className='job-list-str'>:
						</div>
						<ul>
							{job}
						</ul>
					</div>
					<div className="recruit-main inside-main-box">
					
					<div className="recurit-resp" dangerouslySetInnerHTML={resp}></div>
					
					<div className="recruit-requirment" dangerouslySetInnerHTML={requirment}></div>
				</div>
				</div>
			);
	}
}
export default Recruit;



