import React, { Component } from 'react';
import './banner.css';

class Banner extends Component {
	componentDidMount() {
	}
	render() {
		return (
				<div className = 'banner' id='banner'>
					<div className="banner-box">
						<img src='https://www.vanqled.net/skin/images/1469422286.png' alt='banner' style={{paddingLeft: "35%",paddingTop: "5%", width: "15%"}}>
						</img>
					</div>
					<div className="banner-text">
						<span>LED</span>
					</div>
				</div>
			);
	}
}
export default Banner;



